
.dialogs {
    grid-template-columns: repeat(3, 10fr);
    margin: 30px auto;
    grid-gap: 10px;
    display: grid;

}



.dialog {
    padding-top: 10px;
    padding-left: 10px;
    color: black;
    text-decoration: none;

}

.dialogsItems .active {
    color: gold;
    text-decoration: none;
    padding-left: 10px;
}

.messages {

    padding-top: 5px;
}

.textarea {
    height: 25px;
    width: 250px;

}
